___CSS_LOADER_EXPORT___.push([module.id, `/* daftar */
.list {
  display: grid;
  row-gap: 30px;
  column-gap: 30px;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  padding: 0px;
}
/* card */
.card {
  z-index: 1;
  padding: 10px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  border-radius: 5px;
}

.card a{
  color: black;
  text-decoration: none;
}

.card img {
  width: 100%;
}

.rating {
  position: absolute;
  top: 8px;

  padding: 5px;
  background-color: #b3541e;
  color: aliceblue;
  font-weight: bold;
}

.poster {
  position: relative;
  margin: 0 auto;
}

.deskripsi {
  padding: 10px;
}

.paragraf {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
}
.kota {
  font-size: 20px;
  position: absolute;
  bottom: 0;
  background: rgb(0, 0, 0); /* Fallback color */
  background: rgba(0, 0, 0, 0.5); /* Black background with 0.5 opacity */
  color: #f1f1f1;
  width: 100%;
}

.name {
  font-size: 25px;
}

.name a:hover {
  color: #b3541e;
}

/* skip content */
.skip-link {
  position: absolute;
  top: -40px;
  left: 0;
  background-color: #bf1722;
  color: white;
  padding: 8px;
  z-index: 100;
}

.skip-link:focus {
  top: 0;
}
